<!--  -->
<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card" style="margin-top: 15px">
      <el-table :data="rightslist" border style="width: 100%">
        <el-table-column type="index" label="#" width="80"> </el-table-column>
        <el-table-column prop="authName" label="" width="180">
        </el-table-column>
        <el-table-column prop="path" label=""> </el-table-column>
        <el-table-column prop="level" label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.level == 0"></el-tag>
            <el-tag type="success" v-if="scope.row.level == 1"></el-tag>
            <el-tag type="warning" v-if="scope.row.level == 2"></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rightslist: [],
    }
  },
  created() {
    this.getRightslist()
  },
  methods: {
    async getRightslist() {
      const { data: res } = await this.$http.get('rights/list')
      if (res.meta.status != 200) return this.$message.error('')
      this.rightslist = res.data
    },
  },
}
</script>
