<!--  -->
<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card" style="margin-top: 15px">
      <el-row :gutter="20">
        <el-col :span="15"
          ><div class="grid-content bg-purple">
            <el-input
              placeholder=""
              v-model="pramas.query"
              @clear="getOrderlist"
              clearable
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="getOrderlist"
              ></el-button>
            </el-input></div
        ></el-col>
      </el-row>
      <el-table :data="orderlist" style="width: 100%; margin-top: 47px" border>
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="order_number" label="" width="350px">
        </el-table-column>
        <el-table-column prop="order_price" label="" width="100px">
        </el-table-column>
        <el-table-column prop="pay_status" label="" width="100px">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.pay_status == '0'"
              ></el-tag
            >
            <el-tag v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="100px">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editdialog(scope.row)"
            ></el-button>
            <!--  -->
            <el-button
              type="success"
              icon="el-icon-location"
              size="mini"
              @click="getprogress"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pramas.pagenum"
        :page-sizes="[2, 4, 5, 10]"
        :page-size="pramas.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
      <!--  -->

      <el-dialog title="" :visible.sync="editdialogVisible" width="30%">
        <el-form
          :model="editForm"
          :rules="editFormrule"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="/" prop="location">
            <el-cascader
              clearable
              @change="handleChange"
              v-model="editForm.location"
              :options="city"
              :props="{
                expandTrigger: 'hover',
              }"
            ></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input v-model="editForm.address"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editdialogVisible = false"> </el-button>
          <el-button type="primary" @click="editdialogVisible = false"
            > </el-button
          >
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="progressVisible" width="50%">
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in progressInfo"
            :key="index"
            :timestamp="activity.time"
          >
            {{ activity.context }}
          </el-timeline-item>
        </el-timeline>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import city from './citydata.js'
export default {
  data() {
    return {
      city,
      pramas: {
        query: '',
        pagenum: 1,
        pagesize: 10,
      },
      total: 0,
      orderlist: [],
      editdialogVisible: false,
      editForm: {
        address: '',
        location: [],
      },
      editFormrule: {
        location: [{ required: true, message: '', trigger: 'blur' }],
        address: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
      progressInfo: [
        {
          time: '2018-05-10 09:39:00',
          ftime: '2018-05-10 09:39:00',
          context: ',,',
          location: '',
        },
        {
          time: '2018-05-10 08:23:00',
          ftime: '2018-05-10 08:23:00',
          context:
            '[]  95338',
          location: '',
        },
        {
          time: '2018-05-10 07:32:00',
          ftime: '2018-05-10 07:32:00',
          context: ' []',
          location: '',
        },
        {
          time: '2018-05-10 02:03:00',
          ftime: '2018-05-10 02:03:00',
          context:
            '[], []',
          location: '',
        },
        {
          time: '2018-05-09 23:05:00',
          ftime: '2018-05-09 23:05:00',
          context: ' []',
          location: '',
        },
        {
          time: '2018-05-09 21:21:00',
          ftime: '2018-05-09 21:21:00',
          context: '[], []',
          location: '',
        },
        {
          time: '2018-05-09 13:07:00',
          ftime: '2018-05-09 13:07:00',
          context: ' ',
          location: '',
        },
        {
          time: '2018-05-09 12:25:03',
          ftime: '2018-05-09 12:25:03',
          context: '',
          location: '',
        },
        {
          time: '2018-05-09 12:22:24',
          ftime: '2018-05-09 12:22:24',
          context: 'HLA',
          location: '',
        },
        {
          time: '2018-05-08 21:36:04',
          ftime: '2018-05-08 21:36:04',
          context: '',
          location: '',
        },
      ],
      progressVisible: false,
    }
  },
  created() {
    this.getOrderlist()
  },
  methods: {
    async getOrderlist() {
      const { data: res } = await this.$http.get('orders', {
        params: this.pramas,
      })
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.orderlist = res.data.goods
      this.total = res.data.total
      console.log(this.orderlist)
    },
    handleSizeChange(newsize) {
      this.pramas.pagesize = newsize
      this.getOrderlist()
    },
    handleCurrentChange(newnum) {
      this.pramas.pagenum = newnum
      this.getOrderlist()
    },
    handleChange() {},
    editdialog(row) {
      this.editdialogVisible = true
    },
    async getprogress() {
      // const { data: res } = await this.$http.get('/kuaidi/804909574412544580')
      // if (res.meta.status !== 200) {
      //   return this.$message.error('')
      // }
      // this.progressInfo = res.data
      this.progressVisible = true
      // console.log(this.progressInfo)
    },
  },
}
</script>
<style lang='less' scoped>
@import '../../plugins/timeline/timeline.css';
@import '../../plugins/timeline-item/timeline-item.css';
</style>